<template>
  <li class="day-box" v-bind:class="{ opened: dayPassed }" v-on:click="openDay">
    <div class="inner">{{ day }}</div>
  </li>
</template>

<script>
export default {
  data() {
    return {
      isOpened: false,
    };
  },
  props: {
    day: Number,
    dayPassed: Boolean,
  },
  methods: {
    openDay() {
      this.isOpened = true;
      this.$emit('open-day', this.day);
    },
  },
};
</script>

<style scoped>
.inner {
  position: relative;
  width: 100%;
  height: 100%;
  text-align: center;
  transition: transform 0.6s;
  transform-style: preserve-3d;
  border-radius: 15px;
  box-shadow: 0px 0px 5px 2px #3bbcee;
}

.day-box:active .inner {
  transform: rotateY(360deg);
}
.day-box {
  background: rgba(255, 255, 255, 0);
  /* box-shadow: 0px 0px 5px 2px #3bbcee; */
  padding: 0.01rem;
  width: 3rem;
  height: 3rem;
  margin-top: 10px;
  line-height: 3rem;
  color: white;
  font-weight: bold;
  font-family: 'Dancing Script', cursive;
  /* font-size: 3em; */
  text-align: center;
  position: relative;
  z-index: 1;
  border-radius: 15px;
}

.opened {
  background-image: url('../assets/img/SternOhneHintergrund.png');
  background-size: 5rem;
  background-position: -1.8rem -0.9rem;
  background-repeat: no-repeat;
}

.day-box:hover::after {
  position: absolute;
  content: '';
  width: 100%;
  height: 100%;
  z-index: -1;
  background: #3bbcee54;
  box-shadow: 0px 0px 10px 10px #3bbcee;
  top: 0;
  left: 0;
  opacity: 0.5;
  /* animation: mymove 0.5s alternate infinite; */
  animation: blinkingBox 1s ease-in infinite;
  border-radius: 15px;
}
.day-box {
  margin: 0.5rem;
}

@media screen and (min-width: 1100px) {
  .day-box:nth-child(2n + 2) {
    margin-top: 5rem;
  }

  .day-box:nth-child(3n + 3) {
    margin-top: 10rem;
  }

  .day-box:nth-child(3n + 6) {
    margin-top: 7rem;
  }

  .day-box:nth-child(3n + 9) {
    margin-top: 6rem;
  }
}

@keyframes mymove {
  from {
    opacity: 0.1;
    left: 0;
    /* width: 0%; */
  }
  to {
    opacity: 0.5;
    left: 50%;
    /* width: 100%; */
  }
}
@keyframes blinkingBox {
  from {
    opacity: 0.1;
  }
  to {
    opacity: 1;
  }
}
</style>
