<template>
  <div id="text-box" v-bind:class="adventImage">
    <p class="day-text">{{ dayText }}</p>
  </div>
</template>

<script>
export default {
  props: {
    dayText: String,
    day: Number,
    dayPassed: Boolean,
  },
  computed: {
    adventImage: function() {
      if (this.day < 6) var advent = 'first-advent';
      if (this.day >= 6 && this.day < 13) advent = 'second-advent';
      if (this.day >= 13 && this.day < 20) advent = 'third-advent';
      if (this.day >= 20 && this.day < 27) advent = 'fourth-advent';
      if (!this.dayPassed) {
        advent = 'null-advent';
      }
      return advent;
    },
  },
};
</script>

<style scoped>
#text-box {
  color: white;
  font-size: 1.5rem;
  padding: 1rem;
  background: #0f2a4e;
  box-shadow: 0px 0px 5px 2px #3bbcee;
  border-radius: 20% 10px;
}

.day-text {
  margin-bottom: 12em;
}

#text-box::after {
  position: absolute;
  content: ' ';
  top: 0;
  right: 0;
  left: 0;
  bottom: -2.5rem;

  background-size: contain;
  background-position: center bottom;
  background-repeat: no-repeat;

  z-index: -99;
}

#text-box.null-advent::after {
  background-image: url('../assets/img/0advent.svg');
}
#text-box.first-advent::after {
  background-image: url('../assets/img/1advent.svg');
}
#text-box.second-advent::after {
  background-image: url('../assets/img/2advent.svg');
}
#text-box.third-advent::after {
  background-image: url('../assets/img/3advent.svg');
}
#text-box.fourth-advent::after {
  background-image: url('../assets/img/4advent.svg');
}
</style>
