<template>
  <div class="container">
    <div class="heart-svg">
      <p>
        <span style="font-size: 0.8rem;">{{ textUntilCristmas }}</span> <br />
        <span style="font-size: 1.5rem;">{{ getDaysUntilCristmas }} </span> <br />

        <span style="font-size: 0.8rem;">{{ getToday }}</span>
      </p>
    </div>
  </div>
</template>

<script>
var today = new Date();
var christmasDay = new Date('December 25, ' + today.getFullYear());
// var days = [
//   'Sonntag',
//   'Montag',
//   'Dienstag',
//   'Mittwoch',
//   'Donnerstag',
//   'Freitag',
//   'Samstag',
// ];
var months = [];
var months_en = [
  'January',
  'February',
  'March',
  'April',
  'May',
  'June',
  'July',
  'August',
  'September',
  'October',
  'November',
  'December',
];

var months_de = [
  'Januar',
  'Februar',
  'Märtz',
  'April',
  'Mai',
  'Juni',
  'Juli',
  'August',
  'September',
  'Oktober',
  'November',
  'Dezember',
];

var months_ru = [
  'Января',
  'Февраля',
  'Марта',
  'Апреля',
  'Мая',
  'Июня',
  'Июля',
  'Августа',
  'Сентября',
  'Октября',
  'Ноября',
  'Декабря',
];

export default {
  props: {
    userLang: String,
  },
  data() {
    return {
      day: 0,
      textUntilCristmas: '',
      daysText: '',
      // untilChristmas: this.dueTest,
    };
  },
  computed: {
    getToday: function() {
      this.translate();
      var day =
        // days[today.getDay()] +
        ' ' + today.getDate() + '. ' + months[today.getMonth()];
      return day;
    },
    getDaysUntilCristmas: function() {
      var diff = christmasDay.getTime() - today.getTime();
      var daysdiff = Math.floor(diff / (1000 * 60 * 60 * 24));
      return daysdiff;
    },
  },
  methods: {
    translate() {
      months = months_de;
      this.textUntilCristmas = 'Tage bis Weihnachten';
      this.daysText = 'Tage';
      if (this.userLang == 'en') {
        months = months_en;
        this.textUntilCristmas = 'Days until Christmas';
        this.daysText = 'days';
      }
      if (this.userLang == 'ru') {
        months = months_ru;
        this.textUntilCristmas = 'Дней до Рождества';
        this.daysText = 'дней';
      }
    },
  },
};
</script>

<style>
.container {
  position: absolute;
  bottom: 5vh;
  filter: drop-shadow(0px 0px 5px #3bbcee);
  cursor: pointer;
  animation: blinkingBox 1s linear infinite;
}

.heart-svg {
  clip-path: circle(5%);
  /* clip-path: polygon(5% 5%, 100% 0%, 100% 75%, 75% 75%, 75% 100%, 50% 75%, 0% 75%); */
  background: #0f2a4e;
  /* animation: flashingLights 1s linear infinite; */
  animation: resolvingHeart 2s ease-in-out forwards;
  animation-iteration-count: 1;
  animation-delay: 1s;
}
.heart-svg p {
  /* border: 0.1em solid transparent; */
  color: #bcddf5;
  text-shadow: 0px 0px 10px #3bbcee;
  padding: 2.1em 1em;
  /* padding-bottom: 4rem; */
  margin: auto;
}

@keyframes blinkingBox {
  from {
    filter: drop-shadow(0px 0px 5px rgb(240, 166, 166));
  }
  to {
    filter: drop-shadow(0px 0px 5px #3bbcee);
  }
}
@keyframes resolvingHeart {
  0% {
    filter: drop-shadow(0px 0px 5px #3bbcee);
    clip-path: circle(1);
  }
  99% {
    clip-path: circle(40%);
  }
  100% {
    clip-path: url(#heartPath);
  }
}
@keyframes flashingLights {
  from {
    border: rgba(255, 17, 0, 0.4) dotted 0.2rem;
  }
  to {
    border: #3bbcee dotted 0.2rem;
  }
}
</style>
