<template>
  <div id="app">
    <div class="header">
      <h1 id="title">
        {{ title }}
      </h1>

      <!-- languages-dropdown -->
      <languages-dropdown
        :selected="userLang"
        :selected-languages="['en', 'de', 'ru']"
        v-on:change="langSelected"
        display-text="native"
        show-selected-native
        :show-search-input="false"
        btn-bg-color="transparent"
      >
      </languages-dropdown>
    </div>

    <calendar v-bind:userLang="userLang" v-bind:debugModeOn="debugModeOn" />
    <heart :userLang="userLang" />
    <impressum />
  </div>
</template>

<script>
import Calendar from '@/components/Calendar.vue';
import Impressum from '@/components/Impressum.vue';
import Heart from '@/components/Heart.vue';
import LanguagesDropdown from 'vue-languages-dropdown';

// the next lines are needed in mobile version to set the viewport correctly
// this affects the #app in css properties
// mope info https://css-tricks.com/the-trick-to-viewport-units-on-mobile/
let vh = window.innerHeight * 0.01;
document.documentElement.style.setProperty('--vh', `${vh}px`);
//

export default {
  comments: true,
  name: 'App',
  components: {
    Calendar,
    Impressum,
    Heart,
    'languages-dropdown': LanguagesDropdown,
  },
  data() {
    return {
      title: 'Adventskalender',
      userLang: navigator.language.slice(0, 2) || navigator.userLanguage.slice(0, 2),
      loaded: false,
      debugModeOn: '',
    };
  },
  mounted() {
    let uri = window.location.search.substring(0);
    let params = new URLSearchParams(uri);

    this.debugModeOn = params.has('testme') ? 'testme' : '';
    this.translateText();
    // this.loaded = true;
  },
  methods: {
    langSelected(data) {
      console.log(data.code);
      this.userLang = data.code;
      this.translateText();
    },
    translateText() {
      if (this.userLang == 'de') {
        this.title = 'Adventskalender';
      } else if (this.userLang == 'ru') {
        this.title = 'Рождественский календарь';
      } else {
        this.title = 'Advent calendar';
      }
    },
  },
};
</script>

<style>
@import url('https://fonts.googleapis.com/css2?family=Dancing+Script:wght@600&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Open+Sans:wght@300;400;600&display=swap');

.flag.flag-at {
  background-position: -96px -33px !important;
}

.flag.flag-am {
  background-position: -240px -121px !important ;
}

.selected-lang-text {
  color: transparent;
}

.dropbtn {
  min-width: 50px;
  width: 50px;
}

.dropdown {
  /* display: inline; */
  z-index: 99;
}
.dropdown-content {
  position: absolute;
  right: 0;
}

.header {
  display: flex;
  align-items: end;
}

@media screen and (max-width: 640px) {
  html {
    font-size: 10px;
  }
  #app::after {
    background-position: -190px;
  }
}

body {
  margin: 0;
}

#title {
  margin: 0;
  /* padding-top: 1rem; */
  padding-right: 0.5rem;
  color: #ebe4dc;
}

#app {
  position: relative;
  height: 100vh; /* Fallback for browsers that do not support Custom Properties */
  height: calc(var(--vh, 1vh) * 100);
  z-index: 1;

  display: grid;
  justify-items: center;

  font-family: 'Comic Sans MS', cursive, sans-serif;
  text-align: center;

  background-color: #4a5ecc;
}

#app::after {
  content: '';

  position: absolute;
  height: 100%;
  width: 100%;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: -1;

  background-image: url('./assets/img/Optimized_HintergrundBild_Web.jpg');
  background-size: cover;
  background-repeat: no-repeat;
  background-attachment: fixed;
  background-position-x: -270px;
  opacity: 0.8;
}
</style>
