<template>
  <div id="calendar">
    <div class="day-text-box-container">
      <day-text-box
        v-if="dayOpened"
        class="day-text-box"
        v-bind:dayText="dayText"
        v-bind:day="dayToOpen"
        :dayPassed="dayPassedList[dayToOpen]"
      />
    </div>
    <ul class="days-container">
      <!-- li component -->
      <day class="day" v-for="day in days" :key="day" :day="day" :dayPassed="dayPassedList[day]" @open-day="openDay" />
    </ul>
  </div>
</template>

<script>
import Day from '@/components/CalendarDay';
import DayTextBox from '@/components/DayExtendedTextBox.vue';

export default {
  components: {
    Day,
    DayTextBox,
  },
  props: {
    userLang: String,
    debugModeOn: String,
  },
  data() {
    return {
      dayOpened: false,
      dayPassedList: this.initDayPassedList(),
      days: [10, 19, 7, 2, 8, 15, 20, 24, 5, 1, 14, 21, 11, 18, 23, 3, 16, 17, 6, 22, 12, 4, 9, 13],
      dayText: '',
      dayToOpen: null,
    };
  },

  methods: {
    openDay(day) {
      this.dayToOpen = day;
      this.dayOpened = false;
      if (this.userLang == 'en') {
        this.dayText = 'You have to wait until you are welcome to open a window!';
      } else if (this.userLang == 'ru') {
        this.dayText = 'Нужно немного подождать, пока вы сможете открыть окошко!';
      } else {
        this.dayText = 'Sie müssen noch warten, bis Sie ein Fensterchen öffnen dürfen!';
      }

      fetch(`${process.env.VUE_APP_API_URL}/api/days/${day}?lang=${this.userLang}&${this.debugModeOn}`)
        .then((response) => response.json())
        .then((json) => {
          this.dayText = json.text ? json.text : this.dayText;
          this.dayPassedList[day] = json.text ? true : false;
          localStorage.dayPassedList = JSON.stringify(this.dayPassedList);
          this.dayOpened = true;
        });
    },
    initDayPassedList() {
      //return passed days from localStorage or an array object
      if (localStorage.dayPassedList) {
        return JSON.parse(localStorage.dayPassedList);
      }
      return [];
    },
  },
};
</script>

<style scoped>
.days-container {
  display: flex;
  flex-flow: row wrap;
  justify-content: space-around;

  width: 40%;

  padding: 0;
  margin: 0;
  margin-right: 4rem;
  list-style: none;
  align-content: center;
}

.day-text-box-container {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 60%;
  position: relative;
}

.day-text-box {
  position: absolute;
  /* min-height: 60%; */
  width: 50%;

  animation: displayDayTextBox 2s alternate forwards;
}

#calendar {
  display: flex;
  align-content: space-between;

  /* margin-right: 5rem; */

  height: 80vh;
  /* height: max-content; */
  width: 100vw;
}

.day:hover {
  /* transform: rotateX(180deg);
  perspective: 1000px; */
}

@media screen and (max-width: 640px) {
  #calendar {
    display: flex;
    flex-direction: column-reverse;
    justify-content: start;
  }

  .days-container {
    width: 100%;
  }

  .day-text-box-container {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 100%;
    position: relative;
  }

  .day-text-box {
    position: relative;
    /* min-height: 90%; */
    width: 80%;

    animation: displayDayTextBox2 2s alternate forwards;
  }
}

/* keyframes */
@keyframes displayDayTextBox {
  from {
    opacity: 0;
    left: -50%;
  }
  to {
    opacity: 0.9;
    left: 25%;
  }
}
@keyframes displayDayTextBox2 {
  from {
    opacity: 0;
    bottom: -10%;
  }
  to {
    opacity: 0.9;
    bottom: 0%;
  }
}
</style>
